body {
  margin: 0;
  padding: 0;
  color: var(--color-a);
  background-color: var(--body-bg);
  background-image: url(../img/main-bg.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  width: 100%;
  position: relative;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  min-height: -webkit-fill-available;
  display: flex;
  flex-direction: column;
}

html {
  box-sizing: border-box;
  width: 100%;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  min-height: 100vh;
  min-height: -webkit-fill-available;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

main {
  padding: clamp(20px, 5.403vw, 67px) 0 clamp(20px, 8.065vw, 100px);
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  display: inline-flex;
  color: inherit;
  font-weight: inherit;
  font-style: inherit;
  font-family: inherit;
  text-decoration: none;
  cursor: pointer;
  margin: 0;
}

button {
  background-color: transparent;
  border: none;
  display: inline-flex;
  outline: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  line-height: initial;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

picture {
  display: inline-flex;
}

input {
  width: 100%;
  outline: none;
  border: none;
}

label {
  display: block;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
  position: relative;
  padding: 0 10px;
}
@media (min-width: 1440px) and (max-width: 1920px) {
  .container {
    max-width: 1440px;
  }
}
.container--header {
  max-width: 1620px;
}
@media (min-width: 1440px) and (max-width: 1920px) {
  .container--header {
    max-width: 1440px;
  }
}

* {
  scrollbar-color: #EDF3F6;
}
*::-webkit-scrollbar {
  width: 9px;
  box-shadow: none;
  height: 100%;
  display: none;
}
*::-webkit-scrollbar-thumb {
  background-color: #3C93F0;
  border-radius: 4px;
  background-clip: padding-box;
  border: 2px solid #EDF3F6;
  box-shadow: none;
}
*::-webkit-scrollbar-thumb:hover {
  cursor: pointer;
}
*::-webkit-scrollbar-track {
  background-color: #EDF3F6;
  cursor: pointer;
  border-radius: 6px;
  box-shadow: none;
  height: 100%;
}
* ::-webkit-scrollbar-button {
  background-color: transparent;
  box-shadow: none;
  display: none;
}

.main-wrapper {
  display: grid;
  grid-template-columns: minmax(300px, 1fr) 3.7fr;
  max-width: 1620px;
  width: 100%;
  padding: 0 10px;
  margin: 0 auto;
  grid-gap: min(5vw, 80px);
}
@media (min-width: 1440px) and (max-width: 1920px) {
  .main-wrapper {
    max-width: 1440px;
    grid-gap: 50px;
  }
}
@media (max-width: 1440px) {
  .main-wrapper {
    grid-gap: clamp(20px, 3.125vw, 50px);
  }
}
@media (max-width: 1024px) {
  .main-wrapper {
    grid-template-columns: 100%;
  }
}

.main-sections {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(20px, 3.125vw, 50px);
}

.section {
  padding: clamp(15px, 1.875vw, 30px) clamp(15px, 1.563vw, 25px);
  background: var(--section__bg);
  border-radius: 10px;
}

.main-aside {
  position: sticky;
}
@media (max-width: 1024px) {
  .main-aside {
    width: 300px;
    position: fixed;
    left: -100%;
  }
}

.general-title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(30px, 3.25vw, 52px);
  line-height: 106%;
  color: var(--general-title);
}

.general-text {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(16px, 1.25vw, 20px);
  line-height: 140%;
  color: var(--general-text);
}

.bottom-line {
  position: relative;
  width: 100%;
  padding-bottom: 40px;
}
.bottom-line::before {
  position: absolute;
  content: "";
  background-image: var(--subtitle-line);
  background-repeat: no-repeat;
  background-position: center;
  bottom: 0;
  height: 14px;
  width: 60%;
  left: 50%;
  transform: translateX(-50%);
}

.icon-title {
  position: relative;
  padding-top: calc(clamp(30px, 3.516vw, 45px) + 15px);
  text-align: center;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(30px, 4.063vw, 52px);
  line-height: 106%;
  color: var(--general-title);
}
.icon-title::before {
  position: absolute;
  content: "";
  width: clamp(30px, 3.516vw, 45px);
  height: clamp(30px, 3.516vw, 45px);
  background-image: var(--icon-title);
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  top: 0;
  left: 50%;
  z-index: 2;
  transform: translateX(-50%);
}

.join-btn {
  flex-shrink: 0;
  width: clamp(120px, 10.484vw, 130px);
  height: clamp(30px, 3.629vw, 45px);
  font-style: normal;
  font-weight: 700;
  font-size: clamp(16px, 1.452vw, 18px);
  line-height: 100%;
  font-family: var(--font-family);
  color: var(--color-a);
  text-transform: uppercase;
  background-color: var(--join-btn);
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.more-btn {
  flex-shrink: 0;
  width: clamp(120px, 11.29vw, 140px);
  height: clamp(30px, 3.468vw, 43px);
  font-style: normal;
  font-weight: 700;
  font-size: clamp(16px, 1.452vw, 18px);
  line-height: 100%;
  font-family: var(--font-family);
  color: var(--color-b);
  text-transform: uppercase;
  background-image: var(--transparent-btn);
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}

.aside-btn {
  flex-shrink: 0;
  width: 100%;
  height: clamp(35px, 4.597vw, 57px);
  font-style: normal;
  font-weight: 700;
  font-size: clamp(16px, 1.452vw, 18px);
  line-height: 100%;
  font-family: var(--font-family);
  color: var(--color-b);
  text-transform: uppercase;
  background-image: var(--transparent-btn);
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}

.gradient-btn {
  flex-shrink: 0;
  width: clamp(140px, 10.5vw, 186px);
  height: clamp(40px, 3.5vw, 56px);
  font-style: normal;
  font-weight: 700;
  font-size: clamp(16px, 1.452vw, 18px);
  line-height: 100%;
  font-family: var(--font-family);
  color: var(--color-b);
  text-transform: uppercase;
  background-image: var(--gradient-btn);
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}

.breadcrumbs {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  grid-gap: 25px 12px;
}
.breadcrumbs__link {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  color: rgba(255, 255, 255, 0.6);
}
.breadcrumbs__item:not(:last-child) .breadcrumbs__link {
  position: relative;
}
.breadcrumbs__item:not(:last-child) .breadcrumbs__link::after {
  content: "/";
  color: currentColor;
  margin-left: 12px;
}

header {
  background-color: var(--header-bg);
}

.header {
  display: grid;
  grid-template-columns: clamp(500px, 44.032vw, 546px) auto;
  justify-content: space-between;
}

.header-gradient {
  background: var(--header-gradient);
  padding: 8px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: clamp(15px, 3.226vw, 40px);
}
.header-gradient__descr {
  margin-left: clamp(10px, 1.774vw, 22px);
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(16px, 1.452vw, 18px);
  line-height: 130%;
  color: var(--color-a);
  opacity: 0.5;
}

.header-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: clamp(15px, 2.016vw, 25px);
  max-width: 510px;
  display: none;
}
.header-info__button {
  margin-left: auto;
}
.header-info__vidjet {
  display: flex;
  align-items: center;
  grid-gap: 20px;
}
.header-info__descr {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(16px, 1.452vw, 18px);
  line-height: 111%;
  color: var(--color-c);
}
.header-info__value {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(16px, 1.452vw, 18px);
  line-height: 111%;
  color: var(--color-a);
  display: flex;
  align-items: center;
  grid-gap: 15px;
}
.header-info__value-profit {
  color: #23FF8C;
  font-weight: 700;
}

.main-aside__logo svg {
  fill: transparent;
  width: 100%;
}
.main-aside__logo + .main-aside__list {
  margin-top: clamp(20px, 2.419vw, 30px);
}
.main-aside__item {
  background: var(--aside-item__bg);
  border-radius: 10px;
}
.main-aside__list {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(20px, 2.419vw, 30px);
}
.main-aside__item {
  display: flex;
  flex-direction: column;
  padding: clamp(20px, 2.903vw, 36px) clamp(15px, 3.952vw, 49px) clamp(20px, 3.629vw, 45px);
}
.main-aside__item--nav {
  padding: clamp(20px, 3.629vw, 45px) clamp(15px, 3.226vw, 40px);
}
.main-aside__item--image {
  margin-top: 140px;
  position: relative;
}
.main-aside__image {
  margin-top: -180px;
  margin-bottom: 28px;
  margin-left: -24%;
  margin-right: -24%;
}
.main-aside__image img, .main-aside__image picture {
  width: 100%;
}
.main-aside__title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 700;
