  padding-bottom: 10px;
}
.nft-list__item--grey .nft-list__inner::before {
  display: none;
  align-items: center;
}
.nft-list__item--grey .nft-list__article {
  justify-content: center;
}
.nft-list__item--grey .nft-list__image {
  position: initial;
  transform: none;
  height: auto;
  width: auto;
}
.nft-list__inner {
  position: relative;
  padding: clamp(20px, 4.688vw, 60px) clamp(20px, 3.125vw, 40px);
