@media (max-width: 575px) {
  .nft-list__inner {
    grid-template-columns: 100%;
    justify-content: center;
  }
}
.nft-list__article {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(15px, 1.563vw, 20px);
  grid-column: 2;
}
@media (max-width: 575px) {
  .nft-list__article {
    grid-column: 1;
    text-align: center;
  }
}
.nft-list__image {
  position: absolute;
  width: clamp(161px, 28vw, 420px);
  height: clamp(142px, 24.667vw, 370px);
  top: 50%;
  left: min(2vw, 65px);
  transform: translateY(-50%);
}
@media (max-width: 1024px) {
  .nft-list__image {
    position: initial;
    transform: none;
  }
}
@media (max-width: 575px) {
  .nft-list__image {
    margin: 0 auto;
  }
}
.nft-list__title {
  font-size: clamp(30px, 3.75vw, 48px);
  color: var(--color-a);
}
.nft-list__text {
  color: var(--color-a);
}

.ticket-section {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(25px, 3vw, 45px);
  position: relative;
  z-index: 0;
  padding: clamp(20px, 6.667vw, 100px) clamp(20px, 4vw, 60px) clamp(40px, 6.8vw, 102px);
}
.ticket-section__top {
  display: grid;
  grid-template-columns: auto min(30vw, 450px);
  grid-gap: clamp(40px, 4.667vw, 70px);
  align-items: center;
  justify-content: center;
}
.ticket-section__title {
  font-weight: normal;
  text-transform: uppercase;
}
.ticket-section__text {
  text-align: center;
}
.ticket-section__text--upp {
  text-transform: uppercase;
}
.ticket-section__buttons {
  display: flex;
  grid-gap: 30px;
  justify-content: center;
}
.ticket-section__button {
  max-width: 220px;
}
.ticket-section::before {
  position: absolute;
  content: "";
  width: 90%;
  height: 80%;
  opacity: 0.4;
  background: radial-gradient(50% 50% at 50% 50%, #30448B 0%, rgba(38, 54, 111, 0) 100%);
  z-index: -1;
  pointer-events: none;
  left: 50%;
  top: 10%;
  transform: translateX(-50%);
}

.update-box {
  position: relative;
  z-index: 0;
}
.update-box::before {
  position: absolute;
  content: "";
  width: 90%;
  height: 80%;
  opacity: 0.4;
  background: radial-gradient(50% 50% at 50% 50%, #30448b 0, rgba(38, 54, 111, 0) 100%);
  z-index: -1;
  pointer-events: none;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.update-box__inner {
  overflow: hidden;
  position: relative;
  padding: 20px;
  z-index: 0;
}
.update-box__inner::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border: 0.1px solid transparent;
  border-image-source: var(--border-form);
  border-image-slice: 20;
  border-image-width: 20px;
  border-image-repeat: stretch;
  z-index: -1;
}
.update-box .thanks-box {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: all 0.4s ease-in-out;
  transition-delay: 0.5s;
}
.update-box .thanks-box .update-form__top {
  margin: 0 auto clamp(25px, 6.154vw, 80px);
  grid-gap: 30px;
}
.update-box .thanks-box.show {
  visibility: visible;
  opacity: 1;
  transition: all 0.4s ease-in-out;
  transition-delay: 0.5s;
}
.update-box .thanks-box__icon {
  margin-bottom: clamp(30px, 4.923vw, 64px);
}

.update-form {
  opacity: 1;
  visibility: visible;
  transition: all 0.4s ease-in-out;
  transition-delay: 0.5s;
  padding: clamp(40px, 9.375vw, 96px) min(6.154vw, 158px) clamp(40px, 11.426vw, 117px);
}
.update-form.hide {
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease-in-out;
  transition-delay: 0.5s;
}
.update-form__top {
  max-width: 800px;
  margin: 0 auto clamp(25px, 4.231vw, 55px);
  text-align: center;
}
.update-form__title {
  font-weight: 700;
  font-size: clamp(20px, 4.688vw, 48px);
  text-align: center;
  text-transform: uppercase;
}
.update-form__text {
  font-size: clamp(16px, 2.148vw, 22px);
}
.update-form__inner {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  grid-gap: clamp(20px, 3.077vw, 40px);
  margin-bottom: clamp(30px, 6.462vw, 84px);
}
.update-form__label {
  display: flex;
  width: 100%;
  flex-direction: column;
  grid-gap: 5px;
  align-items: flex-start;
}
.update-form__legend {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(16px, 1.385vw, 18px);
  line-height: 100%;
  color: rgba(245, 214, 143, 0.7);
}
.update-form__input {
  border: 1px solid var(--color-m);
  border-radius: 10px;
  height: clamp(40px, 6.154vw, 80px);
  background: transparent;
  padding: 0 clamp(15px, 2.462vw, 32px);
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: clamp(18px, 1.846vw, 24px);
  line-height: 100%;
  color: var(--color-m);
}
.update-form__button {
  margin: 0 auto;
  width: clamp(140px, 19.692vw, 256px);
  height: clamp(40px, 5.923vw, 77px);
}

.footer-social {
  display: flex;
  grid-gap: 15px;
}
.footer-social__item {
  width: 37px;
  height: 43px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--social-border);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.footer-nav__list {
  display: flex;
  flex-wrap: wrap;
  max-width: 700px;
  margin-left: auto;
  grid-gap: 30px 60px;
  justify-content: flex-end;
}
.footer-nav__item {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: rgba(207, 187, 133, 0.8);
}
.footer-nav__item:not(:first-child) {
  position: relative;
}
.footer-nav__item:not(:first-child)::before {
  position: absolute;
  content: "";
  width: 1px;
  height: 110%;
  background-color: currentColor;
  top: 50%;
  left: -30px;
  transform: translate(100%, -50%);
}

footer {
  max-width: 1620px;
  width: 100%;
  padding: 0 10px;
  margin: 0 auto;
}
@media (max-width: 1440px) {
  footer.main-wrapper {
    max-width: 100%;
    grid-template-columns: 100%;
  }
}

.footer {
  border-top: 1px solid rgba(218, 205, 156, 0.7);
  padding-top: clamp(20px, 2.5vw, 40px);
  padding-bottom: clamp(20px, 2.5vw, 40px);
  max-width: 100%;
  width: 100%;
  margin-left: auto;
  grid-column: span 2;
  display: grid;
  grid-template-columns: auto auto;
  justify-content: space-between;
  grid-template-areas: "logo nav" "text social";
  grid-gap: 20px 50px;
}
@media (max-width: 1440px) {
  .footer {
    max-width: 100%;
    grid-column: initial;
  }
}
@media (max-width: 1024px) {
  .footer {
    grid-column: initial;
  }
}
@media (max-width: 992px) {
  .footer {
    grid-template-areas: "logo social" "nav nav" "text text";
  }
}
@media (max-width: 600px) {
  .footer {
    grid-template-areas: "logo logo" "social social" "nav nav" "text text";
  }
}
.footer__logo {
  grid-area: logo;
  align-self: center;
  font-family: var(--font-family3);
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 100%;
  text-transform: uppercase;
  color: var(--color-h);
}
@media (max-width: 600px) {
  .footer__logo {
    justify-content: center;
    text-align: center;
  }
}
.footer__text {
  grid-area: text;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: rgba(207, 187, 133, 0.5);
}
@media (max-width: 992px) {
  .footer__text {
    text-align: center;
  }
}
.footer__social {
  grid-area: social;
  margin-left: auto;
  display: none;
}
@media (max-width: 600px) {
  .footer__social {
    margin: 0 auto;
  }
}
.footer__nav {
  grid-area: nav;
}
@media (max-width: 992px) {
  .footer__nav-list {
    justify-content: center;
    margin: 0 auto;
    grid-gap: 30px;
  }
  .footer__nav-list > li::before {
    display: none;
  }
  .footer__nav-list > li {
    margin-left: 0px;
  }
}